import MainLayout from "./layout/MainLayout"
import Home from "./page/home"
import Dashboard from "./page/dashboard"



const routers = [
    {
        path: '/',
        component: MainLayout,
        routers: [
            {
                path: '/dashboard',
                component: Dashboard
            },
            {
                path: '/',
                component: Home,
                exact: true
            },
        ]
    },


]

export default routers