import React from 'react'
import { Link } from 'react-router-dom'

export default function Header() {
    return (
        <>
            <div className="header">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-xl-12">
                            <div className="navigation">
                                <nav className="navbar navbar-expand-lg navbar-light bg-light">
                                    <a className="navbar-brand" href="/"><img src="images/logo.png" alt="" /></a>
                                    <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNavDropdown" aria-controls="navbarNavDropdown" aria-expanded="false" aria-label="Toggle navigation">
                                        <span className="navbar-toggler-icon" />
                                    </button>
                                    <div className="collapse navbar-collapse" id="navbarNavDropdown">
                                        <ul className="navbar-nav">
                                            <li className="nav-item">
                                                <a className="nav-link" href="/" data-scroll-nav={0}>Home</a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link" href="#" data-scroll-nav={1}>Price</a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link" href="#" data-scroll-nav={2}>Portfolio </a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link" href="#" data-scroll-nav={3}>Testimonial</a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link" href="#" data-scroll-nav={4}>App</a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link" href="#" data-scroll-nav={5}>Blog</a>
                                            </li>
                                            <li className="nav-item">
                                                <a className="nav-link" href="#" data-scroll-nav={6}>Support</a>
                                            </li>
                                            <li className="nav-item">
                                                <Link to="/dashboard" className="nav-link" >Dashboard</Link>
                                            </li>
                                        </ul>
                                    </div>
                                    <div className="signin-btn">
                                        <a className="btn btn-header" href="signin.html">Sign in</a>
                                        <a className="btn btn-header" href="signin.html">Sign up</a>
                                    </div>
                                    <div className="lang">
                                        <div className>
                                            <img className='langimg' src='./images/Vector-Smart-Object.png' />
                                        </div>
                                        <div className>
                                            <img className='langimg' src="./images/Vector-Smart-Object1.png" alt="" />
                                        </div>
                                        <div className="lang_current">
                                            <span style={{ fontSize: "16px" }}>English</span>
                                            <i><img style={{ width: '10px', marginLeft: '5px' }} src="./images/Vector-Smart-Object2.png" alt="" /></i>
                                        </div>
                                    </div>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
